<template>
  <div class="guide container">
    <div class="nav-bread">{{ $t("navBread.des") }}: > <router-link to="/"> {{ $t("navBread.index") }}  </router-link> > <router-link to="/trade"> {{ $t("navBread.bibiTrade") }} </router-link>> <a  @click="handleGuideRouter" href="javascript:void(0)"> {{ $t("navBread.guide") }} </a> > <a href="javascript:void(0)"> {{ navName }} </a></div>
    <div class="clearfix">
      <div class="navleft pull-left">
        <navleft :navItem="navItem" :navObj="navObj" @getGuideContent="getGuideContent"></navleft>
      </div>
      <div class="r-content pull-right" >
        <div id="guide-title" class="guide-title" v-html="navName">
        </div> 
        <!--  -->
         <div id="guide-content" v-html="content">
           <!-- <p><img src="static/images/index/banner.png" alt=""></p> -->
         </div>
      </div>
    </div>
  </div>
</template>
<script>
  import { getGuideSide, guideContent } from '@/api/guide'
  import navleft from './components/leftNavChange'
  export default {
    components: { navleft },
    data() {
      return {
        navItem: '',
        navName: '',
        navObj: [],
        title: '',
        content: ''
      }
    },
    created() {
      this.stateChange()
      this.init()
    },
    methods: {
      stateChange() {
      },
      init() {
        getGuideSide().then(res => {
          console.log(res)
          if (res.status !== 1000) {
            this.$message.error(res.msg)
            return false
          }
          this.navObj = [...res.data]
          if (this.navObj.length > 0) {
            this.navItem = res.data[0].child[0].name
            this.navName = res.data[0].child[0].name
            this.getGuideContent(this.navObj[0].child[0].id, this.navName)
          }
        }).catch(() => {
  
        })
      },
      getGuideContent(id, name) {
        const data = {}
        data.id = id
        guideContent(data).then(res => {
          console.log(res)
          if (res.status !== 1000) {
            this.navName = name
            this.content = ''
          }
          this.navName = name
          this.content = res.data.content
        }).catch(() => {
  
        })
      },
      handleGuideRouter() {
        window.location.reload()
      }
    }
  }
</script>
<style lang="less" scoped>
  .r-content{
    background: white;
    min-height: 770px;
    padding: 20px 25px;
    width: 917px !important;
    .guide-title{
      padding-top: 9px;
      padding-bottom: 18px;
      border-bottom: 1px solid #e6eaf0;
      font-size: 16px;
    }
    .content img{
      width: 876px !important;
      height: auto;
      border: 1px solid #F5F5F5;
    }
    .content h5{
      padding: 20px 0;
      color: #787878;
    }
    #guide-content{
      padding: 20px;
      /deep/ img{
        width: 100%;
      }
    }
  }
</style>


