<template>
  <div class="nav guide-nav">
   <el-row class="tac">
    <el-col :span="24">
      <el-menu
        :default-active="navItem"
        class="el-menu-vertical-demo"
        @open="handleOpen"
        :router=false
        @close="handleClose">
        <el-submenu :index="item.name"  v-for="item in navObj" :key="item.id">
          <template slot="title">
            <!-- <i><img src="static/images/fabi/people_active.png" alt="" class="nav-icon"></i> -->
            <i><img :src="item.image_url" alt="" class="nav-icon"></i>
            <span>{{ item.name }}</span>
          </template>
          <el-menu-item-group>
            <el-menu-item :index="list.name" v-for="list in item.child" :key="list.id" @click="$emit('getGuideContent', list.id, list.name)">{{ list.name }}</el-menu-item>
          </el-menu-item-group>
        </el-submenu>
      </el-menu>
    </el-col>
  </el-row>
  </div>
</template>
<script>
  export default {
    props: ['navItem', 'navObj'],
    methods: {
      handleOpen(key, keyPath) {
        // console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        // console.log(key, keyPath);
      }
    }
  }
</script>

<style lang="less">
  .guide-nav{
    .nav-icon{
      width: 20px;
      height: 20px;
    }
  }
</style>



